<template>
  <div class="about">
    <h1>This is an about page !@!@!~~</h1>
    <div class="router" ref="router">
      <button @click="handleClick">/a</button>
      <button @click="handleClick">/b</button>
      <button @click="handleClick">/c</button>
      <button @click="handleClick">/d</button>
    </div>
    <div class="router-view" ref="routerview">fdsafas f</div>
  </div>
</template>

<script>
const map = new Map([
  ["/a", "首页"],
  ["/b", "分类"],
  ["/c", "购物车"],
  ["/d", "个人中心"],
]);
export default {
  mounted() {
    window.onhashchange = function (e) {
      e.preventDefault();
      console.log(location.hash);
    };
  },
  methods: {
    handleClick(e) {
      console.log(e.target.innerText);
      this.render(e.target.innerText);
    },
    render(hash) {
      this.$refs.routerview.textContent = map.get(hash);
    },
  },
};
</script>

